"use client";

import React from "react";

interface CardData {
  title?: string;
  description?: string;
  imageUrl?: string;
}

const Card = ({ title, description, imageUrl }: CardData) => {
  return (
    <div className="bg-blue shadow-lg rounded-lg overflow-hidden w-400 h-48 p-4 text-white">
      {imageUrl && <img className="object-cover" src={imageUrl} alt={title} />}
      <div className="p-2">
        <h3 className="text-xl pb-2 font-semibold">{title}</h3>
        <p className="text-xs truncate">{description}</p>
      </div>
    </div>
  );
};

export default Card;
